import NextAuth from "next-auth";
import GoogleProvider from "next-auth/providers/google";
import CredentialsProvider from "next-auth/providers/credentials";
import bcrypt from "bcrypt";
import { openDb, initDb } from "@/lib/db";

const handler = NextAuth({
  providers: [
    CredentialsProvider({
      name: "Credentials",
      credentials: {
        email: { label: "Email", type: "text" },
        password: { label: "Password", type: "password" },
      },
      async authorize(credentials) {
        if (!credentials?.email || !credentials?.password) return null;

        await initDb();
        const db = await openDb();
        const user = await db.get("SELECT * FROM users WHERE email = ?", credentials.email);
        if (!user) return null;

        const match = await bcrypt.compare(credentials.password, user.password_hash);
        if (!match) return null;

        return { id: user.id, name: user.name, email: user.email };
      },
    }),
    GoogleProvider({
      clientId: process.env.GOOGLE_CLIENT_ID!,
      clientSecret: process.env.GOOGLE_CLIENT_SECRET!,
    }),
  ],
  callbacks: {
    async signIn({ user }) {
      await initDb();
      const db = await openDb();
      const existing = await db.get("SELECT * FROM users WHERE email = ?", user.email);
      if (!existing) {
        await db.run(
          "INSERT INTO users (name, email, password_hash) VALUES (?, ?, ?)",
          user.name,
          user.email,
          "" // no password for OAuth users
        );
      }
      return true;
    },
    async jwt({ token, user }) {
      if (user) {
        token.id = user.id;
        token.name = user.name;
        token.email = user.email;
      }
      return token;
    },
    async session({ session, token }) {
      session.user = {
        id: token.id,
        name: token.name,
        email: token.email,
      };
      return session;
    },
  },
  secret: process.env.NEXTAUTH_SECRET,
});

export { handler as GET, handler as POST };
